import React, { useState } from 'react';
import './App.scss';
import { GoodsList } from './GoodsList';
import { Good } from './types/Good';

import { getAll, get5First, getRedGoods } from './api/goods';
// or
// import * as goodsAPI from './api/goods';

export const App: React.FC = () => {
  const [goods, setGoods] = useState<Good[]>([]);
  const [isLoaded, setisLoaded] = useState(false);

  const loadAll = () => {
    getAll().then((goodsServ) => setGoods(goodsServ));
    setisLoaded(true);
  };

  const loadFive = () => {
    get5First().then((goodsServ) => setGoods(goodsServ));
    setisLoaded(true);
  };

  const loadRed = () => {
    getRedGoods().then((goodsServ) => setGoods(goodsServ));
    setisLoaded(true);
  };

  return (
    <div className="App">
      <h1>Dynamic list of Goods</h1>

      <button
        type="button"
        data-cy="all-button"
        onClick={() => {
          loadAll();
        }}
      >
        Load all goods
      </button>

      <button
        type="button"
        data-cy="first-five-button"
        onClick={() => {
          loadFive();
        }}
      >
        Load 5 first goods
      </button>

      <button
        type="button"
        data-cy="red-button"
        onClick={() => {
          loadRed();
        }}
      >
        Load red goods
      </button>

      {isLoaded
        && <GoodsList goods={goods} />}

    </div>
  );
};
